<template>
  <div>
    <el-form
      :model="createBankAccountForm"
      ref="createBankAccountForm"
      class="add-form"
      label-position="top"
      size="small"
      :rules="formRules"
    >
      <el-row :gutter="20" class="margin-bottom-20">
        <el-col :span="12">
          <el-form-item :label="$t('IDC.bankAccount.label.bankHeadOffice')" prop="bankHeadOffice">
            <el-select
              id="bankHeadOffice"
              :placeholder="$t('common.placeholder.select')"
              v-model="createBankAccountForm.bankHeadOffice"
            >
              <el-option
                v-for="item in bankHeadOfficeOptions"
                :label="item.label"
                :value="item.value"
                :key="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('IDC.bankAccount.label.bank')" prop="bankId">
            <el-select
              id="bankId"
              :placeholder="$t('common.placeholder.select')"
              v-model="createBankAccountForm.bankId"
            >
              <el-option v-for="item in bankOptions" :label="item.label" :value="item.value" :key="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <div v-if="showBankBox" class="bank-box">
        <div class="content-container" v-for="item of bankDatas" :key="item.label">
          {{ item.label + ': ' + (item.content ? item.content : '-') }}
        </div>
      </div>
      <el-row :gutter="20" class="margin-bottom-20">
        <el-col :span="12">
          <el-form-item :label="$t('IDC.bankAccount.label.assetType')" prop="assetType">
            <el-select
              id="assetType"
              :placeholder="$t('common.placeholder.select')"
              v-model="createBankAccountForm.assetType"
            >
              <el-option v-for="item in assetTypeOptions" :label="item.label" :value="item.value" :key="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('IDC.bankAccount.label.currency')" prop="currency">
            <el-select
              id="currency"
              :placeholder="$t('common.placeholder.select')"
              v-model="createBankAccountForm.currency"
            >
              <el-option v-for="item in currencyOptions" :label="item.label" :value="item.value" :key="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20" class="margin-bottom-20">
        <el-col :span="12">
          <el-form-item :label="$t('IDC.bankAccount.label.accountName')" prop="accountName">
            <el-input
              id="accountName"
              :placeholder="$t('common.placeholder.enter')"
              v-model.trim="createBankAccountForm.accountName"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('IDC.bankAccount.label.accountNumber')" prop="accountNo">
            <el-input
              id="accountNo"
              :placeholder="$t('common.placeholder.enter')"
              v-model.trim="createBankAccountForm.accountNo"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20" class="margin-bottom-20">
        <el-col :span="12">
          <el-form-item :label="$t('IDC.bankAccount.label.validityPeriod')" prop="validityPeriod" class="date-box">
            <el-date-picker
              v-model="createBankAccountForm.validityPeriod"
              type="daterange"
              value-format="yyyy-MM-dd HH:mm:ss.SSS"
              format="dd/MM/yyyy"
              range-separator="~"
              :start-placeholder="$t('common.placeholder.select')"
              :end-placeholder="$t('common.placeholder.select')"
            >
            </el-date-picker>
            <i :class="['el-icon-date', 'date-icon']"></i>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="$t('IDC.bankAccount.label.transferMode')" prop="transferMode">
            <el-select
              id="transferMode"
              :placeholder="$t('common.placeholder.select')"
              v-model="createBankAccountForm.transferMode"
            >
              <el-option v-for="item in transferModeOptions" :label="item.label" :value="item.value" :key="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="20" class="margin-bottom-20">
        <el-col :span="12">
          <el-form-item :label="$t('IDC.bankAccount.label.iban')" prop="iban">
            <el-input
              id="iban"
              :placeholder="$t('common.placeholder.enter')"
              v-model.trim="createBankAccountForm.iban"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>

<script>
import {
  queryBankList,
  queryDict,
  getTransferMode,
  getBankHeadOfficeList,
  getBankInfo,
} from '@/views/identifier-center/api/api-bank-account.js';
import {
  selectRequiredValidateObj,
  inputRequiredValidateObj,
  nonSpecialCharValidateObj,
  numberAndCharacterValidateObj,
  getStrLenValidateObj,
  len256StrValidateObj,
} from '@/common/validator/common';

export default {
  name: 'BankAccountDialog',
  data() {
    return {
      currencyOptions: [],
      bankHeadOfficeOptions: [],
      bankOptions: [],
      assetTypeOptions: [],
      transferModeOptions: [],
      formRules: {
        bankHeadOffice: [selectRequiredValidateObj],
        bankId: [selectRequiredValidateObj],
        assetType: [selectRequiredValidateObj],
        currency: [selectRequiredValidateObj],
        accountName: [inputRequiredValidateObj, nonSpecialCharValidateObj, len256StrValidateObj],
        accountNo: [
          inputRequiredValidateObj,
          numberAndCharacterValidateObj,
          getStrLenValidateObj({ minLen: 10, maxLen: 64 }),
        ],
        iban: [nonSpecialCharValidateObj, getStrLenValidateObj({ minLen: 0, maxLen: 34 })],
      },
      showBankBox: false,
      bankDatas: [
        {
          label: this.$t('IDC.bankAccount.label.bankShortCode'),
          value: 'bankShortCode',
        },
        {
          label: this.$t('IDC.bankAccount.label.bankAddress'),
          value: 'bankAddress',
        },
        {
          label: this.$t('IDC.bankAccount.label.financeService'),
          value: 'financeService',
        },
        {
          label: this.$t('IDC.bankAccount.label.trustBank'),
          value: 'trustBankCode',
        },
      ],
    };
  },
  props: ['createBankAccountForm', 'identityId'],
  watch: {
    'createBankAccountForm.bankHeadOffice': {
      handler() {
        this.getBankList();
      },
      deep: true,
    },
    'createBankAccountForm.bankId': {
      handler(newVal) {
        if (newVal) {
          this.$delete(this.bankDatas, 'content');
          this.getCurrentBankInfo({ bankId: newVal });
          this.showBankBox = true;
        } else {
          this.showBankBox = false;
        }
      },
      deep: true,
    },
  },
  methods: {
    querySelectList() {
      this.getBankHeadOfficeList();
      this.geCurrencyList();
      this.getAssetTypeList();
      this.getTransferMode();
    },

    async getBankHeadOfficeList() {
      this.bankHeadOfficeOptions = [];
      let res = await getBankHeadOfficeList({});
      if (res.header && res.header.success) {
        let dataDictJson = JSON.parse(res.jsonDataStr);
        dataDictJson.map((item) => {
          let officeObj = {
            value: item.code,
            label: item.value,
          };
          this.bankHeadOfficeOptions.push(officeObj);
        });
      }
    },
    async getBankList() {
      this.bankOptions = [];
      this.createBankAccountForm.bankId = '';
      let params = {
        pageNo: 1,
        pageSize: 100,
        bankHeadOffice: this.createBankAccountForm.bankHeadOffice,
      };
      let res = await queryBankList(params);
      if (res.header && res.header.success) {
        if (res.banks && res.banks.length > 0) {
          res.banks.map((bank) => {
            let bankObj = {
              value: bank.bankId,
              label: bank.bankName,
            };
            this.bankOptions.push(bankObj);
          });
        }
      }
    },
    async geCurrencyList() {
      this.currencyOptions = [];
      let res = await queryDict({ dictType: 'Currency' });
      if (res.header && res.header.success) {
        let dataDictJson = JSON.parse(res.dataDictJson);
        dataDictJson.map((item) => {
          let currencyObj = {
            value: item.code,
            label: item.code,
          };
          this.currencyOptions.push(currencyObj);
        });
      }
    },
    async getAssetTypeList() {
      this.assetTypeOptions = [];
      let res = await queryDict({ dictType: 'AssetType' });
      if (res.header && res.header.success) {
        let dataDictJson = JSON.parse(res.dataDictJson);
        dataDictJson.map((item) => {
          let assetTypeObj = {
            value: item.code,
            label: item.name,
          };
          this.assetTypeOptions.push(assetTypeObj);
        });
      }
    },
    async getTransferMode() {
      this.transferModeOptions = [];
      let res = await getTransferMode();
      if (res.header && res.header.success) {
        let dataDictJson = JSON.parse(res.jsonDataStr);
        dataDictJson.map((item) => {
          let transferModeObj = {
            value: item.code,
            label: item.value,
          };
          this.transferModeOptions.push(transferModeObj);
        });
      }
    },
    async getCurrentBankInfo(data) {
      try {
        let res = await getBankInfo(data);
        if (res && res.header.success) {
          let financeService = [];
          for (const key in res.bank) {
            if (key === 'isDepositBank') {
              if (res.bank[key] === true) {
                financeService.push('Deposit Bank');
              }
            }
            if (key === 'isTrustBank') {
              if (res.bank[key] === true) {
                financeService.push('Trust Bank');
              }
            }
            if (key === 'isCardIssuingBank') {
              if (res.bank[key] === true) {
                financeService.push('Card Issuing Bank');
              }
            }
          }
          let kycGroupList = res.bankKYC.kycGroupList.filter(
            (item) => item.kycGroupId === 1003 && item.kycFieldId === 1007
          );
          this.bankDatas.forEach((item) => {
            if (item.value === 'financeService') {
              item.content = financeService.join(',');
            } else if (item.value === 'trustBankCode') {
              item.content = res.bank.trustBankCode;
            } else if (item.value === 'bankShortCode') {
              item.content = res.bank.bankShortCode;
            } else if (item.value === 'bankAddress') {
              item.content = kycGroupList.length ? kycGroupList[0].inputValue : '';
            }
          });
          this.bankDatas = JSON.parse(JSON.stringify(this.bankDatas));
        }
      } catch (error) {}
    },
  },
  created() {
    this.querySelectList();
  },
  mounted() {},
};
</script>
<style lang="scss" scoped>
::v-deep .el-range__icon {
  display: none;
}

.bank-box {
  display: flex;
  align-content: space-between;
  flex-wrap: wrap;
  background-color: #f0f9ff;
  padding: 12px 1px 0 0;
  margin-top: -16px;
  margin-bottom: 20px;

  .content-container {
    color: $--status_color--notice;
    font-size: 14px;
    width: calc(50% - 10px);
    margin-left: 10px;
