<template>
  <div class="detail-box margin-left-20">
    <div class="container" v-for="item of detailDatas" :key="item.label">
      <div class="subtitle">{{ item.label + ':' }}</div>
      <div class="detail-content">{{ item.content ? item.content : '-' }}</div>
    </div>
  </div>
</template>

<script>
import { queryBankAccountDetail, getTransferMode } from '@/views/identifier-center/api/api-bank-account.js';
import dayjs from 'dayjs';
import { cloneDeep } from 'lodash';

export default {
  name: 'DrawerDetailContent',
  data() {
    return {
      currentRowData: '',
      detailDatas: '',
      activeDetailData: [
        {
          label: this.$t('IDC.bankAccount.label.assetType'),
          value: 'assetType',
        },
        {
          label: this.$t('IDC.bankAccount.label.accountName'),
          value: 'accountName',
        },
        {
          label: this.$t('IDC.bankAccount.label.accountNumber'),
          value: 'accountNo',
        },
        {
          label: this.$t('IDC.bankAccount.label.bankHeadOffice'),
          value: 'bankHeadOffice',
        },
        {
          label: this.$t('IDC.bankAccount.label.bank'),
          value: 'bankName',
        },
        {
          label: this.$t('IDC.bankAccount.label.bankShortCode'),
          value: 'bankShortCode',
        },
        {
          label: this.$t('IDC.bankAccount.label.currency'),
          value: 'currency',
        },
        {
          label: this.$t('IDC.bankAccount.label.isDefault'),
          value: 'default',
        },
        {
          label: this.$t('IDC.bankAccount.label.iban'),
          value: 'iban',
        },
        {
          label: this.$t('IDC.bankAccount.label.transferMode'),
          value: 'transferMode',
        },
        {
          label: this.$t('common.label.status'),
          value: 'status',
        },
        {
          label: this.$t('IDC.bankAccount.label.createdBy'),
          value: 'createOperatorName',
        },
        {
          label: this.$t('IDC.bankAccount.label.creationTime'),
          value: 'createDate',
        },
      ],
      removedDetailAdditionalData: [
        {
          label: this.$t('IDC.bankAccount.label.removedBy'),
          value: 'removeOperatorName',
        },
        {
          label: this.$t('IDC.bankAccount.label.removalTime'),
          value: 'removeDate',
        },
        {
          label: this.$t('IDC.bankAccount.label.remark'),
          value: 'remark',
        },
      ],
    };
  },
  computed: {},
  methods: {
    queryDetailContent(row) {
      Promise.all([this.getTransferModeData(), this.queryAccountDetail(row)])
        .then((values) => {
          const transferModeOptions = values[0];
          const bankAccount = values[1];
          if (!bankAccount) {
            return;
          }
          for (const key in bankAccount) {
            this.detailDatas.forEach((item) => {
              if (item.value === key) {
                if (key === 'createDate' || key === 'removeDate') {
                  let date = bankAccount[key];
                  item.content = dayjs(date).format('DD/MM/YYYY HH:mm');
                } else {
                  if (key === 'assetType') {
                    item.content = row.assetType;
                  } else if (key === 'bankHeadOffice') {
                    item.content = row.bankHeadOffice;
                  } else if (key === 'default') {
                    item.content = bankAccount[key] ? 'Yes' : 'No';
                  } else if (key === 'transferMode' && transferModeOptions) {
                    let resultMode = transferModeOptions.filter((mode) => {
                      return mode.value === bankAccount[key];
                    });
                    if (resultMode.length) {
                      item.content = resultMode[0].label;
                    }
                  } else {
                    item.content = bankAccount[key];
                  }
                }
              }
            });
          }
          this.detailDatas = JSON.parse(JSON.stringify(this.detailDatas));
        })
        .catch(() => {});
    },
    queryAccountDetail(row) {
      this.currentRowData = row;
      this.getDetailDatas();
      let params = {
        tenantId: '-1',
        assetId: row.assetId,
        identityId: row.identityId,
      };
      return new Promise((resolve) => {
        queryBankAccountDetail(params)
          .then((res) => {
            let bankAccount;
            if (res && res.header.success) {
              res.bankAccount['createOperatorName'] = res['createOperatorName'];
              res.bankAccount['removeOperatorName'] = res['removeOperatorName'];
              bankAccount = res.bankAccount;
            }
            resolve(bankAccount);
          })
          .catch(() => {
            return Promise.resolve({});
          });
      });
    },
    getTransferModeData() {
      return new Promise((resolve) => {
        getTransferMode()
          .then((res) => {
            let transferModeOptions = [];
            if (res.header && res.header.success) {
              let dataDictJson = JSON.parse(res.jsonDataStr);
              dataDictJson.map((item) => {
                let transferModeObj = {
                  value: item.code,
                  label: item.value,
                };
                transferModeOptions.push(transferModeObj);
              });
            }
            resolve(transferModeOptions);
          })
          .catch(() => {
            return Promise.resolve([]);
          });
      });
    },
    getDetailDatas() {
      let status = this.currentRowData.status;
      if (status === 'Active') {
        this.detailDatas = cloneDeep(this.activeDetailData);
      } else {
        this.detailDatas = cloneDeep(this.activeDetailData.concat(this.removedDetailAdditionalData));
      }
    },
  },
  created() {},
  mounted() {},
};
</script>

<style lang="scss" scoped>
.detail-box {
  .container {
    display: flex;
    align-content: space-between;
    flex-wrap: wrap;
    margin-top: 18px;
  }

  .subtitle {
    color: $--text_color--secondary;
    font-size: 14px;
    width: 32%;
  }

  .detail-content {
    color: $--text_color--emphasis;
    font-size: 14px;
    width: 68%;
  }
}
</style>
