<template>
  <div class="active-account-content">
    <div>
      <el-button
        :disabled="!hasPerm('IC_ORG_BANK_ACCOUNT_ADD_BTN')"
        id="create-scenario-btn"
        type="primary"
        size="small"
        icon="el-icon-plus"
        style="margin-top: 8px"
        @click="createBtnClick()"
      >
        {{ $t('common.button.add') }}
      </el-button>
      <table-page
        class="margin-top-16"
        size="small"
        :isCheck="false"
        :isCheckMemory="false"
        :isSingleCheck="false"
        :has-pagination="true"
        :isSearch="isSearchList"
        :columns="listColumns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
      >
        <template slot-scope="scope" slot="operation">
          <el-button
            type="text"
            @click="queryDetail(scope.data.row)"
            class="table-operation-btn icon-cursor operation-button"
          >
            {{ $t('common.button.detail') }}
          </el-button>
          <el-button
            :disabled="!hasPerm('IC_ORG_BANK_ACCOUNT_DELETE_BTN')"
            type="text"
            v-preventReClick
            @click="removeAccount(scope.data.row)"
            class="table-operation-btn icon-cursor operation-button"
          >
            {{ $t('common.button.remove') }}
          </el-button>
          <el-button
            type="text"
            v-preventReClick
            @click="setDefault(scope.data.row)"
            v-if="scope.data.row.default === 'No'"
            class="table-operation-btn icon-cursor"
          >
            {{ $t('IDC.bankAccount.button.setDefault') }}
          </el-button>
        </template>
      </table-page>
    </div>
    <!-- 创建弹框 -->
    <portal-dialog
      v-if="dialogVisible"
      :visible="true"
      :title="$t('IDC.bankAccount.pageTitleCreate')"
      :confirmText="$t('common.button.confirm')"
      :width="'800px'"
      :height="'442px'"
      @cancel-btn-click="closeDialog"
      @confirm-btn-click="submit"
      @handle-close="closeDialog"
    >
      <template #content>
        <bank-account-dialog
          ref="bankAccountDialog"
          :createBankAccountForm="createBankAccountForm"
        ></bank-account-dialog>
      </template>
    </portal-dialog>
    <!-- 详情页弹框 -->
    <portal-dialog
      v-if="showDetail"
      :visible="true"
      :title="$t('IDC.bankAccount.pageTitleDetail')"
      :customFooter="true"
      :width="'560px'"
      @handle-close="closeDetailDialog"
    >
      <template #content>
        <div class="detail-dialog-des">{{ $t('IDC.bankAccount.pageDescDetail') }}</div>
        <drawer-detail-content ref="childDrawer" style="margin-left: 4px"></drawer-detail-content>
      </template>
    </portal-dialog>
    <!-- 删除确认框 -->
    <portal-dialog
      top="29vh"
      height="342"
      class="remove-dialog"
      v-if="removeDialog"
      :confirmText="$t('common.button.confirm')"
      :visible="true"
      :title="$t('IDC.bankAccount.removeTitle')"
      :width="'448px'"
      @cancel-btn-click="closeRemoveDialog"
      @confirm-btn-click="submitRemoveDialog"
      @handle-close="closeRemoveDialog"
    >
      <template #content>
        <h1 class="dialog-title subtitle-color">{{ removeDialogTitle }}</h1>
        <el-divider />
        <el-form label-position="top" ref="deleteRoleFormRef" :model="delForm" :rules="deleteRoleFormRules">
          <el-form-item :label="$t('IDC.bankAccount.label.reason')" prop="reason">
            <el-input
              type="textarea"
              prop="reason"
              :rows="4"
              v-model="delForm.reason"
              :placeholder="$t('common.placeholder.enter')"
            />
          </el-form-item>
        </el-form>
      </template>
    </portal-dialog>
  </div>
</template>

<script>
import {
  queryBankAccountList,
  deleteBankAccount,
  createBankAccount,
  setDefaultBankAccount,
} from '@/views/identifier-center/api/api-bank-account.js';
import { inputRequiredValidateObj, len512StrValidateObj } from '@/common/validator/common';
import TablePage from '@/components/tablePage';
import PortalDialog from '@/components/portal-dialog';
import BankAccountDialog from '@/views/identifier-center/pages/bank-account/create/bank-account-dialog';
import DrawerDetailContent from '@/views/identifier-center/pages/bank-account/detail/drawer-detail-content';
import cookie from '@/common/utils/cookie';
export default {
  name: 'ActiveAccountContent',
  props: ['searchForm', 'assetTypeOptions', 'bankHeadOfficeOptions', 'identityId'],
  components: {
    TablePage,
    PortalDialog,
    BankAccountDialog,
    DrawerDetailContent,
  },
  data() {
    return {
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      listColumns: [
        { label: this.$t('IDC.bankAccount.label.assetType'), prop: 'assetType', align: 'left', 'min-width': 200 },
        { label: this.$t('IDC.bankAccount.label.accountName'), prop: 'accountName', align: 'left', 'min-width': 200 },
        { label: this.$t('IDC.bankAccount.label.accountNumber'), prop: 'accountNo', align: 'left', 'min-width': 200 },
        { label: this.$t('IDC.bankAccount.label.currency'), prop: 'currency', align: 'left', 'min-width': 160 },
        {
          label: this.$t('IDC.bankAccount.label.isDefault'),
          prop: 'default',
          align: 'left',
          'min-width': 120,
        },
        {
          label: this.$t('IDC.bankAccount.label.bankHeadOffice'),
          prop: 'bankHeadOffice',
          align: 'left',
          'min-width': 200,
        },
        { label: this.$t('IDC.bankAccount.label.bank'), prop: 'bankName', align: 'left', 'min-width': 187 },
        {
          label: 'Operation',
          prop: 'operation',
          align: 'left',
          fixed: 'right',
          __slotName: 'operation',
          'min-width': 245,
        },
      ],
      isSearchList: false,
      // 创建弹框
      dialogVisible: false,
      showDetail: false,
      removeDialog: false,
      removeBankAccount: '',
      delForm: {
        reason: '',
      },
      deleteRoleFormRules: {
        reason: [inputRequiredValidateObj, len512StrValidateObj],
      },
      createBankAccountForm: {
        assetType: '',
        accountName: '',
        accountNo: '',
        bankHeadOffice: '',
        bankId: '',
        currency: '',
        transferMode: '',
        iban: '',
        validityPeriod: '',
        effectiveDate: '',
        expiryDate: '',
      },
    };
  },
  computed: {
    removeDialogTitle() {
      return this.$t('IDC.bankAccount.removeConfirm', { obj: this.removeBankAccount.accountName }).toString();
    },
  },
  watch: {},
  created() {
    this.isSearchList = true;
  },
  activated() {},
  mounted() {},
  methods: {
    search() {
      this.resetPagination();
      this.isSearchList = true;
    },
    resetPagination() {
      this.paginationConfig = {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      };
    },
    getTableData() {
      let params = {
        status: 'Active',
        accountName: this.searchForm.accountName,
        assetType: this.searchForm.assetType,
        bankHeadOffice: this.searchForm.bankHeadOffice,
        bankName: this.searchForm.bankName,
        identityId: this.identityId,
        pageNo: this.paginationConfig.pageIndex,
        pageSize: this.paginationConfig.pageSize,
      };
      return new Promise((resolve) => {
        queryBankAccountList(params).then((res) => {
          if (res.header && res.header.success) {
            res.totalRecords = res.totalNum || 0;
            res.list = res.bankAccounts || [];
            res.list.forEach((item) => {
              this.assetTypeOptions.forEach((assetType) => {
                if (assetType.value === item.assetType) {
                  item.assetType = assetType.label;
                }
              });
              this.bankHeadOfficeOptions.forEach((bankHeadOffice) => {
                if (bankHeadOffice.value === item.bankHeadOffice) {
                  item.bankHeadOffice = bankHeadOffice.label;
                }
              });
              item.default = item.default ? 'Yes' : 'No';
            });
            this.isSearchList = false;
            resolve(res);
          } else {
            res.totalRecords = 0;
            res.list = [];
            this.isSearchList = false;
            resolve(res);
          }
        });
      });
    },

    queryDetail(row) {
      this.showDetail = true;
      setTimeout(() => {
        let param = {};
        Object.assign(param, row);
        param.identityId = cookie.get('initiatorIdentityId') || this.identityId;
        this.$refs.childDrawer.queryDetailContent(row);
      });
    },

    closeDetailDialog() {
      this.showDetail = false;
    },

    removeAccount(row) {
      this.removeBankAccount = row;
      this.removeDialog = true;
    },

    setDefault(row) {
      this.$confirm(this.$t('IDC.bankAccount.setDefaultTitle'), this.$t('IDC.bankAccount.button.setDefault'), {
        confirmButtonText: this.$t('common.button.confirm'),
        cancelButtonText: this.$t('common.button.cancel'),
        closeOnClickModal: false,
        type: 'info',
        customClass: 'messageBox',
      }).then(() => {
        const params = {
          assetId: row.assetId,
          identityId: this.identityId,
        };
        setDefaultBankAccount(params).then((res) => {
          if (res && res.header.success) {
            this.isSearchList = true;
            this.$modal.msgSuccess('Success to set default account.');
          }
        });
      });
    },

    createBtnClick() {
      this.dialogVisible = true;
    },

    submit() {
      this.$refs.bankAccountDialog.$refs.createBankAccountForm.validate((valid) => {
        if (valid) {
          let params = {
            bankAccount: {
              createOperId: '-1',
              identityId: this.identityId,
              bankHeadOffice: this.createBankAccountForm.bankHeadOffice,
              bankId: this.createBankAccountForm.bankId,
              assetType: this.createBankAccountForm.assetType,
              accountName: this.createBankAccountForm.accountName,
              accountNo: this.createBankAccountForm.accountNo,
              currency: this.createBankAccountForm.currency,
              transferMode: this.createBankAccountForm.transferMode,
              iban: this.createBankAccountForm.iban,
              effectiveDate: this.createBankAccountForm.validityPeriod
                ? this.createBankAccountForm.validityPeriod[0]
                : '',
              expiryDate: this.createBankAccountForm.validityPeriod ? this.createBankAccountForm.validityPeriod[1] : '',
            },
            receiverIdentityId: this.identityId,
          };
          createBankAccount(params)
            .then((res) => {
              if (res && res.header.success) {
                this.dialogVisibleFalse();
                this.search();
              }
            })
            .finally(() => {});
        }
      });
    },
    closeDialog() {
      this.dialogVisibleFalse();
    },

    dialogVisibleFalse() {
      for (const key in this.createBankAccountForm) {
        this.createBankAccountForm[key] = '';
      }
      this.dialogVisible = false;
    },

    // 删除确认框
    closeRemoveDialog() {
      this.removeDialog = false;
      this.delForm.reason = '';
      this.removeBankAccount = '';
    },

    submitRemoveDialog() {
      this.$refs['deleteRoleFormRef'].validate((isValid) => {
        if (isValid) {
          let params = {
            identityId: this.identityId,
            receiverIdentityId: this.identityId,
            assetIds: [this.removeBankAccount.assetId],
            reason: this.delForm.reason,
            remark: this.delForm.reason,
          };
          deleteBankAccount(params)
            .then((res) => {
              if (res && res.header.success) {
                this.search();
                this.$emit('refreshRemovedAccount');
                this.$modal.msgSuccess('Success to delete.');
              }
            })
            .finally(() => {
              this.closeRemoveDialog();
            });
        }
      });
    },
  },
};
</script>

<style scoped lang="scss">
.detail-dialog-des {
  font-size: 12px;
  color: $--text_color--secondary;
  margin-left: 4px;
}

.remove-dialog {
  .subtitle-color {
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: 400;
    color: $--text_color--emphasis;
  }

  /deep/.el-divider--horizontal {
    margin-bottom: 20px;
  }
  /deep/ .el-form-item {
    margin-bottom: 0;
  }
}
/deep/ .is-error {
  margin-bottom: 0 !important;
}
</style>
<style lang="scss">
.messageBox .el-button:nth-child(2) {
  background-color: $--primary_color;
  border-color: $--primary_color;
}
.messageBox .el-button:nth-child(2):hover {
  background: $--primary_color--hover;
  border-color: $--primary_color;
}
.messageBox {
  .el-message-box__status::before {
    color: $--status_color--notice;
  }
}
</style>
