<template>
  <div class="business-portal-page">
    <table-page
      class="margin-top-16"
      size="small"
      :isCheck="false"
      :isCheckMemory="false"
      :isSingleCheck="false"
      :has-pagination="true"
      :isSearch="isSearchRemovedList"
      :columns="removedListColumns"
      :pagination="removedPaginationConfig"
      :getTableData="getRemovedTableData"
    >
      <template slot-scope="scope" slot="operation">
        <!-- Detail按钮 -->
        <el-button type="text" class="btn" @click="queryDetail(scope.data.row)" style="padding: 0">
          {{ $t('common.button.detail') }}
        </el-button>
      </template>
    </table-page>
    <!-- 详情页弹框 -->
    <portal-dialog
      v-if="showDetail"
      :visible="true"
      :title="$t('IDC.bankAccount.pageTitleDetail')"
      :customFooter="true"
      :width="'560px'"
      @handle-close="closeDetailDialog"
    >
      <div slot="title">
        <div class="detail-dialog-des">{{ $t('IDC.bankAccount.pageDescDetail') }}</div>
      </div>
      <div slot="content">
        <drawer-detail-content-second ref="childDrawer" style="margin-left: 4px"></drawer-detail-content-second>
      </div>
      <div slot="footer">
        <div style="height: 24px"></div>
      </div>
    </portal-dialog>
  </div>
</template>

<script>
import { queryBankAccountList } from '@/views/identifier-center/api/api-bank-account.js';
import TablePage from '@/components/tablePage';
import PortalDialog from '@/components/portal-dialog';
import DrawerDetailContentSecond from '@/views/identifier-center/pages/bank-account/detail/drawer-detail-content-second';

export default {
  name: 'RemovedAccountContent',
  props: ['searchForm', 'assetTypeOptions', 'bankHeadOfficeOptions', 'identityId'],
  components: {
    TablePage,
    PortalDialog,
    DrawerDetailContentSecond,
  },
  data() {
    return {
      removedPaginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      removedListColumns: [
        { label: this.$t('IDC.bankAccount.label.assetType'), prop: 'assetType', align: 'left' },
        { label: this.$t('IDC.bankAccount.label.accountName'), prop: 'accountName', align: 'left' },
        { label: this.$t('IDC.bankAccount.label.accountNumber'), prop: 'accountNo', align: 'left' },
        { label: this.$t('IDC.bankAccount.label.currency'), prop: 'currency', align: 'left' },
        {
          label: this.$t('IDC.bankAccount.label.bankHeadOffice'),
          prop: 'bankHeadOffice',
          align: 'left',
        },
        { label: this.$t('IDC.bankAccount.label.bank'), prop: 'bankName', align: 'left' },
        { label: 'Operation', prop: 'operation', align: 'left', __slotName: 'operation', fixed: 'right' },
      ],
      isSearchRemovedList: false,
      showDetail: false,
    };
  },
  computed: {},
  watch: {},
  created() {
    this.isSearchRemovedList = true;
  },
  activated() {},
  mounted() {},
  methods: {
    search() {
      this.resetPagination();
      this.isSearchRemovedList = true;
    },
    resetPagination() {
      this.removedPaginationConfig = {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      };
    },
    getRemovedTableData() {
      let params = {
        status: 'Removed',
        accountName: this.searchForm.accountName,
        assetType: this.searchForm.assetType,
        bankHeadOffice: this.searchForm.bankHeadOffice,
        bankName: this.searchForm.bankName,
        identityId: this.identityId,
        pageNo: this.removedPaginationConfig.pageIndex,
        pageSize: this.removedPaginationConfig.pageSize,
      };
      return new Promise((resolve) => {
        queryBankAccountList(params).then((res) => {
          if (res.header && res.header.success) {
            res.totalRecords = res.totalNum || 0;
            res.list = res.bankAccounts || [];
            res.list.forEach((item) => {
              this.assetTypeOptions.forEach((assetType) => {
                if (assetType.value === item.assetType) {
                  item.assetType = assetType.label;
                }
              });
              this.bankHeadOfficeOptions.forEach((bankHeadOffice) => {
                if (bankHeadOffice.value === item.bankHeadOffice) {
                  item.bankHeadOffice = bankHeadOffice.label;
                }
              });
            });
            this.isSearchRemovedList = false;
            resolve(res);
          } else {
            res.list = [];
            res.totalRecords = 0;
            this.isSearchRemovedList = false;
            resolve(res);
          }
        });
      });
    },

    queryDetail(row) {
      this.showDetail = true;
      setTimeout(() => {
        this.$refs.childDrawer.queryDetailContent(row);
      });
    },

    closeDetailDialog() {
      this.showDetail = false;
    },
  },
};
</script>

<style scoped lang="scss">
.detail-dialog-des {
  font-size: 12px;
  color: $--text_color--secondary;
  margin-left: 4px;
}
</style>
