import request from '@/common/utils/request';

// 枚举类-数据字典
export function queryDict(data) {
  return request({
    url: '/fintech/v1/dict/query',
    method: 'post',
    data,
  });
}

export function getBankHeadOfficeList(data) {
  return request({
    url: '/fintech/v1/bank/getBank',
    method: 'post',
    data,
  });
}

